@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Source+Sans+Pro:wght@300&display=swap');

body {
  margin: 0;
}

p {
  margin: 0;
}

a {
  font-family: 'Poppins';
  font-weight: 600;
  text-decoration: none;
  display: block;
  position: relative;
}

a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: white;
  transition: opacity 200ms, transform 200ms;
  transform: scale(0);
  transform-origin: center;
}

a:hover::after, a:focus::after {
  transform: scale(1);
}

.header-background {
  background: linear-gradient(135deg, #4a62ed, #e35656);
  padding-top: 200px;
}

.header-slash {
  width: 0;
  height: 0;
  border-top: 200px solid transparent;
  border-right: 100vw solid white;
}

.content {
  margin-top: -400px;
  display: flex;
  justify-content: center;
}

.content-column {
  width: 100%;
  max-width: 800px;
  padding: 15px;
}

.content-contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.content-contacts-link {
  color: white;
  font-size: 15px;
}

.content-bio {
  display: flex;
  margin-top: 25px;
  justify-content: space-between;
  flex-wrap: wrap;
}

.content-bio-text {
  margin-bottom: 25px;
  margin-right: 15px;
}

.content-bio-text-header {
  font-family: 'Poppins';
  font-size: 45px;
  color: white;
}

.content-bio-text-tag {
  font-family: 'Source Sans Pro';
  font-size: 25px;
  color: white;
}

.content-bio-profile {
  min-width: 300px;
  width: 300px;
  height: 300px;
  border-radius: 20px;
  overflow: hidden;
}

@media only screen and (min-width: 600px) {
  .content-bio {
    margin-top: 67px;
  }
}

@media only screen and (max-width: 600px) {
  .content-bio-profile {
    margin-left: auto;
    margin-right: auto;
  }
}
